body{
    font-family: var(--title-fonts);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;

    color: var(--text-color);

    position: relative;
}

a{
    text-decoration: none;
    color: currentColor;
}

h1, h2, h3, h4, p{
    margin: 0;
}

img{
    display: block;
    max-width: 100%;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

svg{
    fill: currentColor;
}

address{
    font-style: normal;
}